body{
    box-sizing: border-box;
    font-family: 'Baloo Tammudu 2', cursive;
    background-image: linear-gradient(to left bottom, #160b23, #180e26, #1a1028, #1c122b, #1f142e, #21162f, #241930, #261b31, #281f30, #292330, #2b262f, #2c2a2e);
}
.header{
    background-image: url(../images/banner2.jpg);
    width: 100%;
    height: 30em;
    background-size: cover;
    text-transform: uppercase;
    .barraNav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        .logo{
        width: 150px;
        height: 150px;
        padding: 15px 15px 0 15px;
        margin-left: 10px;;
        }
        .btnNav{
            
            a{
                text-decoration: none;
                color: blanco;
            }
        }
    }
    
}